import {
    FormConfiguration,
    defaultFormConfiguration,
  } from '@/src/lib/formConfigurations/formConfigurationTypes';
  import {
    getDoc,
    setDoc,
    doc,
    serverTimestamp,
    updateDoc,
  } from 'firebase/firestore';
  import { db } from '@/src/firebaseConfig';
  
  /**
   * L·∫•y c·∫•u h√¨nh form theo companyId.
   * N·∫øu kh√¥ng t·ªìn t·∫°i th√¨ tr·∫£ v·ªÅ c·∫•u h√¨nh m·∫∑c ƒë·ªãnh.
   */
  export const getFormConfigurationByCompanyId = async (
    companyId: string
  ): Promise<FormConfiguration> => {
    const docRef = doc(db, 'formConfigurations', companyId);
    const snapshot = await getDoc(docRef);
  
    if (snapshot.exists()) {
      return {
        id: snapshot.id,
        ...snapshot.data(),
      } as FormConfiguration;
    } else {
      return {
        ...defaultFormConfiguration,
        companyId,
      };
    }
  };
  
  /**
   * L∆∞u c·∫•u h√¨nh form (t·∫°o m·ªõi ho·∫∑c c·∫≠p nh·∫≠t).
   * @param config FormConfiguration
   */
  export const saveFormConfiguration = async (
    config: FormConfiguration
  ): Promise<void> => {
    const docRef = doc(db, 'formConfigurations', config.companyId);
    const dataToSave = {
      ...config,
      updatedAt: serverTimestamp(),
    };
  
    if (config.id) {
      // C·∫≠p nh·∫≠t (update)
      await updateDoc(docRef, dataToSave);
    } else {
      // T·∫°o m·ªõi
      await setDoc(docRef, {
        ...dataToSave,
        createdAt: serverTimestamp(),
      });
    }
  };
  